
import React from 'react';
import { CarouselProvider, Slider, Slide, ButtonBack, ButtonNext } from 'pure-react-carousel';
import 'pure-react-carousel/dist/react-carousel.es.css';
function Projects() {
    return (
             
        <div className="body" >
            <h1 className="about">Projects</h1>
            <div className="skills-container">
            <h1 className="skills js">JavaScript</h1>
            <h1 className="skills html">HTML</h1>
            <h1 className="skills css">CSS</h1>
            </div>
            <CarouselProvider
        naturalSlideWidth={125}
        naturalSlideHeight={55}
        totalSlides={2}
        visibleSlides={1}
        orientation={"vertical"}
        className="container">
            <Slider className="slider" >
            <div className="project-container">
            <Slide index={1} className="project-container-slider">
            <div>
            <h2 className="project-container-text" >01</h2>
            <a href="https://andmarmuk.github.io/Tic-tac-toe/" className="project-container-text">Tic-tac-toe</a>
            <h1 className="project-text" > Tic-tac-toe is played on a three-by-three grid by two players</h1>
            </div>

            </Slide>
            <Slide index={2} className="project-container-slider">
            <div>
            <h2 className="project-container-text" >02</h2>
            <a href="https://andmarmuk.github.io/calculator/" className="project-container-text">Calculator</a>
            <h1 className="project-text" > Simple calculator</h1>
            </div>
            </Slide>
            </div>
            </Slider>
            <div className='buttons'>
            <ButtonBack >up</ButtonBack>
            <ButtonNext>down</ButtonNext>
            </div>
            </CarouselProvider>

        </div>
 );
};

export default Projects;