
import React from 'react';
import { CarouselProvider, Slider, Slide, ButtonBack, ButtonNext } from 'pure-react-carousel';
import 'pure-react-carousel/dist/react-carousel.es.css';
function Works() {
    return (
             
        <div className="body" >
            <h1 className="about">Works</h1>
            <div className="skills-container">
            <h1 className="skills js">Freelance</h1>
            <h1 className="skills html">Wordpress</h1>
            <h1 className="skills css">Simple projects</h1>
            </div>
            <CarouselProvider
        naturalSlideWidth={125}
        naturalSlideHeight={55}
        totalSlides={2}
        visibleSlides={1}
        orientation={"vertical"}
        className="container">
            <Slider className="slider" >
            <div className="project-container">
            <Slide index={0} className="project-container-slider">
            <a href="https://strum.com.pl/" className="project-container-text"></a>
            <div>
            <h2 className="project-container-text" >01</h2>
            <a href="https://strum.com.pl/" className="project-container-text">Strum</a>
            <h1 className="project-text" >  Designing, creating and developing the web page for ceramic studio</h1>
            </div>
            
            </Slide>
            <Slide index={1} className="project-container-slider">
            <a href="/projects/cloudmine" className="project-container-text"></a>
            <div>
            <h2 className="project-container-text" >02</h2>
            <a href="https://cloudmine.com.pl/" className="project-container-text">Cloudmine</a>
            <h1 className="project-text" > Developing the web page for online shop</h1>
            </div>
            </Slide>
            </div>
            </Slider>
            <div className='buttons'>
            <ButtonBack >up</ButtonBack>
            <ButtonNext>down</ButtonNext>
            </div>
            </CarouselProvider>
            
        </div>
 );
};

export default Works;